___CSS_LOADER_EXPORT___.push([module.id, `/* Base (Mobile) Styles */
.service-page {
  position: relative;
  font-family: "Helvetica", sans-serif;
}

/* --- Hero Section --- */
.service-hero {
  position: relative;
  width: 100%;
  height: 90vh;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  color: #fff;
}

.services-hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
}

.services-hero-content-title {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.company-name-services {
  font-size: 0.7rem !important;
  font-weight: lighter;
  letter-spacing: 1.5px;
  margin: 0 12px;
  padding: 40px 20px;
}

.line {
  width: 30px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.5);
  margin: 20px;
}

.services-hero-overlay h1 {
  font-size: 2.6rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(119, 116, 148, 0.6);
  width: 70%;
  margin: 40px auto;
}

.services-hero-overlay p {
  font-size: 1.1rem;
  line-height: 1.4;
  max-width: 600px;
  margin: 0 auto 1.5rem auto;
}

/* CTA Button */
.cta-button {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background-color: #00a38c;
  color: #fff;
  border-radius: 5px;
  text-decoration: none;
  font-weight: 600;
  transition: background-color 0.3s;
}
.cta-button:hover {
  background-color: #008977;
}

/* --- Overlay Images --- */
/* Overlay images are only rendered on mobile (<768px) */
.overlay-images {
  position: absolute;
  top: 100vh;
  left: 50%;
  transform: translateX(-50%);
  z-index: 5;
  width: 300px;
  height: 200px;
}

.overlay-images .image1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 70%;
  border-radius: 6px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.overlay-images .image2 {
  position: absolute;
  top: 18vh;
  left: 35%;
  width: 70%;
  border-radius: 6px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* --- Main Content Section --- */
.service-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.content-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

/* Provider Container */
.provider-container {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  margin-top: 1rem;
  align-items: center;
}

.provider-text {
  flex: 1;
}

.provider-image img {
  width: 100%;
  max-width: 80px;
  border-radius: 4px;
  display: block;
}

/* Content Image */
.content-image {
  width: 100%;
  min-width: 100vw;
  border-radius: 8px;
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 80%,
    rgba(0, 0, 0, 0) 100%
  );
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0) 100%);
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
}

#services-title-small {
  margin: 4rem 0 0 0;
  text-align: left;
  font-size: 2.5rem;
}

.content-text {
  flex: 1;
  font-size: 1rem;
  line-height: 1.6;
  color: #333;
}
.content-text p {
  margin-bottom: 1rem;
}

/* --- Info Section --- */
.info-section {
  max-width: 1200px;
  margin: 2rem auto 0 auto;
  padding: 2rem;
  background-size: cover;
  background-position: center;
  border-radius: 8px;
  position: relative;
}

.info-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 2rem;
  border-radius: 8px;
  color: #fff;
}

.info-title {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  color: #fff;
}

.info-subtitle {
  font-size: 1.4rem;
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
  color: #fff;
}

.info-text {
  font-size: 1rem;
  line-height: 1.6;
  color: #fff;
  margin-bottom: 1rem;
}

.info-list {
  list-style: none;
  padding-left: 0;
  margin: 1rem 0;
}

.info-list li {
  position: relative;
  padding-left: 2rem;
  margin-bottom: 0.75rem;
}

.info-list li::before {
  content: "✓";
  position: absolute;
  left: 0;
  top: 0;
  color: #00a38c;
  font-weight: bold;
}

.info-overlay h2,
.info-overlay h3,
.info-overlay p,
.info-overlay li {
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.7);
}

@media (min-width: 768px) {
  /* Hero can be shorter on desktop */
  .service-hero {
    height: 70vh;
  }
  .services-hero-overlay h1 {
    font-size: 3rem;
    width: 60%;
    margin: 40px auto;
  }
  .services-hero-overlay p {
    font-size: 1.2rem;
    max-width: 700px;
  }

  /* Although styles exist for overlay images here, they won't be rendered on desktop */
  .overlay-images {
    position: absolute;
    top: 810px;
    left: 50%;
    transform: translateX(-50%);
    width: 500px;
    height: 300px;
    z-index: 5;
  }
  
  .overlay-images .image1 {
    position: absolute;
    top: 0;
    left: 0;
    width: 45%;
    border-radius: 6px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }
  
  .overlay-images .image2 {
    position: absolute;
    top: 30%;
    left: 45%;
    width: 50%;
    border-radius: 6px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }
  .service-content-with-title {
      display: flex;
      flex-direction: column;
      align-items: center;
  }
  /* Main content section: display side by side */
  .content-section {
    flex-direction: column;
    align-items: flex-start;
    margin-top: 6rem;
  }
  .content-image {
    width: 100%;
    min-width: 600px;
    -webkit-mask-image: none;
    mask-image: none;
  }
  .content-text {
    width: 50%;
    margin-left: 2rem;
    margin-left: 60px;
  }
  #services-title-small {
    margin-top: 0;
    font-size: 2.8rem;
    width: 50%;
    margin-left: 60px;

  }
  .info-section {
    margin-bottom: 40px;
  }
}

@media (min-width: 1024px) {
  .content-section {
    flex-direction: row;
  } 
}`, "",{"version":3,"sources":["webpack://./src/pages/Services.css"],"names":[],"mappings":"AAAA,yBAAyB;AACzB;EACE,kBAAkB;EAClB,oCAAoC;AACtC;;AAEA,yBAAyB;AACzB;EACE,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,sBAAsB;EACtB,2BAA2B;EAC3B,aAAa;EACb,mBAAmB;EACnB,6BAA6B;EAC7B,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,WAAW;EACX,YAAY;EACZ,oCAAoC;EACpC,aAAa;EACb,sBAAsB;EACtB,2BAA2B;EAC3B,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,qBAAqB;AACvB;;AAEA;EACE,4BAA4B;EAC5B,oBAAoB;EACpB,qBAAqB;EACrB,cAAc;EACd,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,WAAW;EACX,0CAA0C;EAC1C,YAAY;AACd;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,mBAAmB;EACnB,iDAAiD;EACjD,UAAU;EACV,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,gBAAgB;EAChB,0BAA0B;AAC5B;;AAEA,eAAe;AACf;EACE,qBAAqB;EACrB,uBAAuB;EACvB,yBAAyB;EACzB,WAAW;EACX,kBAAkB;EAClB,qBAAqB;EACrB,gBAAgB;EAChB,iCAAiC;AACnC;AACA;EACE,yBAAyB;AAC3B;;AAEA,2BAA2B;AAC3B,wDAAwD;AACxD;EACE,kBAAkB;EAClB,UAAU;EACV,SAAS;EACT,2BAA2B;EAC3B,UAAU;EACV,YAAY;EACZ,aAAa;AACf;;AAEA;EACE,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,UAAU;EACV,kBAAkB;EAClB,wCAAwC;AAC1C;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,SAAS;EACT,UAAU;EACV,kBAAkB;EAClB,wCAAwC;AAC1C;;AAEA,iCAAiC;AACjC;EACE,iBAAiB;EACjB,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,SAAS;AACX;;AAEA,uBAAuB;AACvB;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,WAAW;EACX,eAAe;EACf,kBAAkB;EAClB,cAAc;AAChB;;AAEA,kBAAkB;AAClB;EACE,WAAW;EACX,gBAAgB;EAChB,kBAAkB;EAClB;;;;GAIC;EACD,mFAAmF;EACnF,8BAA8B;EAC9B,sBAAsB;AACxB;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,OAAO;EACP,eAAe;EACf,gBAAgB;EAChB,WAAW;AACb;AACA;EACE,mBAAmB;AACrB;;AAEA,yBAAyB;AACzB;EACE,iBAAiB;EACjB,wBAAwB;EACxB,aAAa;EACb,sBAAsB;EACtB,2BAA2B;EAC3B,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,oCAAoC;EACpC,aAAa;EACb,kBAAkB;EAClB,WAAW;AACb;;AAEA;EACE,iBAAiB;EACjB,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,iBAAiB;EACjB,kBAAkB;EAClB,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,WAAW;EACX,mBAAmB;AACrB;;AAE